'use strict';

// MODULES //

import { isPrimitive as isNumber } from '@stdlib/assert-is-number';
import isObject from '@stdlib/assert-is-plain-object';
import { isPrimitive as isString } from '@stdlib/assert-is-string';
import isnan from '@stdlib/math-base-assert-is-nan';
import hasOwnProp from '@stdlib/assert-has-own-property';
import contains from '@stdlib/assert-contains';
import format from '@stdlib/error-tools-fmtprodmsg';


// VARIABLES //

var ALTERNATIVE = [
	'two-sided',
	'less',
	'greater'
];


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination object
* @param {Options} options - function options
* @param {number} [options.alpha] - significance level
* @param {string} [options.alternative] - alternative hypothesis
* @param {number} [options.mu] - mean under `H0`
* @returns {(null|Error)} null or an error object
*
* @example
* var options = {
*     'alpha': 0.05
* };
* var opts = {};
*
* var err = validate( opts, options );
* if ( err ) {
*     throw err;
* }
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '1Lr2V', options ) );
	}
	if ( hasOwnProp( options, 'alpha' ) ) {
		opts.alpha = options.alpha;
		if ( !isNumber( opts.alpha ) || isnan( opts.alpha ) ) {
			return new TypeError( format( '1Lr8P', 'alpha', opts.alpha ) );
		}
		if ( opts.alpha < 0.0 || opts.alpha > 1.0 ) {
			return new RangeError( format( '1Lr8V', 'alpha', opts.alpha ) );
		}
	}
	if ( hasOwnProp( options, 'alternative' ) ) {
		opts.alternative = options.alternative;
		if ( !isString( opts.alternative ) ) {
			return new TypeError( format( '1Lr2W', 'alternative', opts.alternative ) );
		}
		if ( !contains( ALTERNATIVE, opts.alternative ) ) {
			return new Error( format( '1Lr4S', 'alternative', ALTERNATIVE.join( '", "' ), opts.alternative ) );
		}
	}
	if ( hasOwnProp( options, 'mu' ) ) {
		opts.mu = options.mu;
		if ( !isNumber( opts.mu ) || isnan( opts.mu ) ) {
			return new TypeError( format( '1Lr8P', 'mu', opts.mu ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
